  .field-group * {
      box-sizing: border-box;
  }
  .field-group { 
    position:relative; 
    margin-bottom:45px;
    margin-top: 20px; 
  }
  .field-group input 				{
    font-size:18px;
    padding:10px 10px 10px 5px;
    display:block;
    width:90px;
    border:none;
    background: #8bcaca;
    border-bottom:1px solid #528c8c;
    color: #ffffff;
  }
  input:focus { 
      outline:none; 
  }
  
  .field-group label {
    color:#528c8c; 
    font-size:16px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.2s ease all; 
    -moz-transition:0.2s ease all; 
    -webkit-transition:0.2s ease all;
  }
  
  .field-group input:focus ~ label, .field-group input:valid ~ label 		{
    top:-20px;
    font-size:14px;
    color:#528c8c;
  }
  
  .field-group .bar 	{ position:relative; display:block; width:90px; }
  .field-group .bar:before, .bar:after 	{
    content:'';
    height:2px; 
    width:0;
    bottom:1px; 
    position:absolute;
    background:#528c8c; 
    transition:0.2s ease all; 
    -moz-transition:0.2s ease all; 
    -webkit-transition:0.2s ease all;
  }
  .field-group .bar:before {
    left:50%;
  }
  .field-group .bar:after {
    right:50%; 
  }
  
  .field-group input:focus ~ .bar:before, .field-group input:focus ~ .bar:after {
    width:50%;
  }
  
  .field-group .highlight {
    position:absolute;
    height:60%; 
    width:100px; 
    top:25%; 
    left:0;
    pointer-events:none;
    opacity:0.5;
  }
  
  .field-group  input:focus ~ .highlight {
    -webkit-animation:inputHighlighter 0.3s ease;
    animation:inputHighlighter 0.3s ease;
  }
  
  @-webkit-keyframes inputHighlighter {
      from { background:#528c8c; }
    to 	{ width:0; background:transparent; }
  }
  @keyframes inputHighlighter {
      from { background:#528c8c; }
    to 	{ width:0; background:transparent; }
  }
.board-wrapper { 
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.board {
    display: flex;
    flex-direction: column;
    align-items: center;
    border:50px solid white;
    background: white;
    position: relative;
}
.row {
    display:flex;
}
.cell {
    width: 45px;
    height: 45px;
    border:0;
    float: left;
    position: relative;
}
.cell button {
    z-index: 60;
    width: 100%;
    height: 100%;
    outline: none;
    display:inline-block;
    -webkit-transition: opacity 0.2s ease-out;
    transition: opacity 0.2s ease-out;
    cursor: pointer;
    border: 0;
    position: relative;
}
.cell.open button {
    opacity: 0;
    cursor: auto;
}
.cell .cell-content {
    font-size: 18px;
    font-weight: 700;
    display: inline-block;
    font-family: 'Architects Daughter', cursive;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    text-align: center;
    padding-top: 10px;
}

.cell.open {
    box-shadow: inset 0px 0px 1px 0px rgba(171,171,171,1);
    cursor: auto;
}
.cell .flag {
    background-image: url(/static/media/red_flag.1c6cbace.svg);
    background-size: cover;    
    background-repeat: no-repeat;
    background-position: center center;  
    width: 30px;
    height: 30px;
    position: absolute;
    z-index: 999;
    top: -5px;
    left: 15px;
    pointer-events: none;
}
.superman-mode .cell:not(.open) button {
    opacity: 0.7;
}
.cell button.dark {
    background: #dd5050;
}
.cell button.light {
    background: #ef5d5d;
}
.cell.dark {
    background: #f3f3f3;
}
.cell.light {
    background: #fbfbfb;
}

