.led-red {
    margin: 0 auto;
    margin-top: 5px;
    width: 15px;
    height: 15px;
    background-color: #F00;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.1) 0 -1px 7px 1px, inset #8a0c0c 0 -1px 9px, rgba(255, 0, 0, 0.5) 0 2px 12px;
    -webkit-animation: blinkRed 1s infinite;
    animation: blinkRed 1s infinite;
    margin-bottom: 15px;
}

@-webkit-keyframes blinkRed {
    from { background-color: #F00; }
    50% { background-color: #A00; box-shadow: rgba(0, 0, 0, 0.1) 0 -1px 7px 1px, inset #8a0c0c 0 -1px 9px, rgba(255, 0, 0, 0.5) 0 2px 0;}
    to { background-color: #F00; }
}
@keyframes blinkRed {
    from { background-color: #F00; }
    50% { background-color: #A00; box-shadow: rgba(0, 0, 0, 0.1) 0 -1px 7px 1px, inset #8a0c0c 0 -1px 9px, rgba(255, 0, 0, 0.5) 0 2px 0;}
    to { background-color: #F00; }
}

.hide {
    display: none;
}
.nav-button {
    position: absolute;
    cursor: pointer;
    width: 0; 
    height: 0; 
}
.nav-button.Up {
  top: -30px;
  border-bottom: 20px solid #195f5f;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
}
.nav-button.Down {
    bottom: -30px;   
    border-top: 20px solid #195f5f;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
}
.nav-button.Right {
    right:-30px;
    top: 50%;
    border-left: 20px solid #195f5f;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
}
.nav-button.Left {
    top:50%;
    left: -30px;
    border-right: 20px solid #195f5f;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
}
.nav-button.Left:hover {
    border-right: 20px solid #319a9a;
}
.nav-button.Right:hover {
    border-left: 20px solid #319a9a;
}
.nav-button.Up:hover {
    border-bottom: 20px solid #319a9a;
}
.nav-button.Down:hover {
    border-top: 20px solid #319a9a;
}

.popup {
