    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0,0,0, 0.7);
    z-index: 1020;
  }
  .popup-inner {
    position: absolute;
    left: 25%;
    right: 25%;
